import React from 'react';
import Tab from './Tab';

const Tabs = ({ tabs, onTabSelected, activeTab }) => (
  <>
    <div>
      {tabs.map((tab, index) => (
        <Tab
        onTabSelected={onTabSelected}
        key={tab.title}
        index={index}
        activeTab={activeTab}
        title={tab.title}
      />
      ))}
    </div>
    <div>
      {tabs[activeTab].content}
    </div>
  </>
)

export default Tabs;
